import React from 'react';

interface WhatsAppChatProps {
  phoneNumber: string;
  message?: string;
  position?: 'bottom-right' | 'bottom-left' | 'top-right' | 'top-left';
  style?: React.CSSProperties;
}

const WhatsAppChat: React.FC<WhatsAppChatProps> = ({
  phoneNumber,
  message = 'Hello! I would like to know more about your services.',
  position = 'bottom-right',
  style
}) => {
  const handleClick = () => {
    // Remove all non-numeric characters and ensure it starts with country code
    const formattedPhone = phoneNumber.replace(/\D/g, '');
    
    // Ensure the phone number starts with country code (e.g., 91 for India)
    // If it doesn't start with a country code, you might want to add your default country code
    const phoneWithCountryCode = formattedPhone.startsWith('91') ? formattedPhone : `91${formattedPhone}`;
    
    // Properly encode the message for URL
    const encodedMessage = encodeURIComponent(message);
    
    // Create the WhatsApp URL
    const whatsappUrl = `https://wa.me/${phoneWithCountryCode}?text=${encodedMessage}`;
    
    // Open WhatsApp in a new tab
    window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
  };

  // We will now control position purely with inline styles
  // const positionClasses = {
  //   'bottom-right': 'bottom-4 right-4',
  //   'bottom-left': 'bottom-4 left-4',
  //   'top-right': 'top-4 right-4',
  //   'top-left': 'top-4 left-4',
  // };

  // Determine base positioning based on the 'position' prop
  const basePositionStyles: React.CSSProperties = {};
  if (position === 'bottom-right') {
    basePositionStyles.bottom = '30px';
    basePositionStyles.right = '30px';
  } else if (position === 'bottom-left') {
    basePositionStyles.bottom = '30px';
    basePositionStyles.left = '30px';
  } else if (position === 'top-right') {
    basePositionStyles.top = '30px';
    basePositionStyles.right = '30px';
  } else if (position === 'top-left') {
    basePositionStyles.top = '30px';
    basePositionStyles.left = '30px';
  }

  return (
    <button
      onClick={handleClick}
      className="fixed rounded-full p-3 shadow-lg transition-all duration-300 ease-in-out hover:scale-110 flex items-center justify-center"
      aria-label="Chat on WhatsApp"
      style={{
        width: '60px',
        height: '60px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 99999,
        background: 'linear-gradient(45deg, #25D366, #128C7E)',
        ...basePositionStyles,
        ...style
      }}
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="32"
        height="32"
        viewBox="0 0 24 24"
        fill="white"
        style={{ 
          display: 'block',
          filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.2))'
        }}
      >
        <path d="M.057 24l1.687-6.163c-1.041-1.804-1.588-3.849-1.587-5.946.003-6.556 5.338-11.891 11.893-11.891 3.181.001 6.167 1.24 8.413 3.488 2.245 2.248 3.481 5.236 3.48 8.414-.003 6.557-5.338 11.892-11.893 11.892-1.99-.001-3.951-.5-5.688-1.448l-6.305 1.654zm6.597-3.807c1.676.995 3.276 1.591 5.392 1.592 5.448 0 9.886-4.434 9.889-9.885.002-5.462-4.415-9.89-9.881-9.892-5.452 0-9.887 4.434-9.889 9.884-.001 2.225.651 3.891 1.746 5.634l-.999 3.648 3.742-.981zm11.387-5.464c-.074-.124-.272-.198-.57-.347-.297-.149-1.758-.868-2.031-.967-.272-.099-.47-.149-.669.149-.198.297-.768.967-.941 1.165-.173.198-.347.223-.644.074-.297-.149-1.255-.462-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.297-.347.446-.521.151-.172.2-.296.3-.495.099-.198.05-.372-.025-.521-.075-.148-.669-1.611-.916-2.206-.242-.579-.487-.501-.669-.51l-.57-.01c-.198 0-.52.074-.792.372s-1.04 1.016-1.04 2.479 1.065 2.876 1.213 3.074c.149.198 2.095 3.2 5.076 4.487.709.306 1.263.489 1.694.626.712.226 1.36.194 1.872.118.571-.085 1.758-.719 2.006-1.413.248-.695.248-1.29.173-1.414z" />
      </svg>
    </button>
  );
};

export default WhatsAppChat; 